body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background: #00000034;
  }
  
  .container {
    max-width: 800px;
    margin: 0 auto;
    padding: 2rem;
  }
  
  h1 {
    text-align: center;
    color: #333;
  }
  
  .form {
    display: block;
    background: #00000063;
    padding: 25px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0,0,0,0.1);
    margin-bottom: 20px;
    box-shadow: 10px 10px 10px 10px ;
    
  }
  
  .form-title,
  .form-text {
    
    display: block;
    width: 97%;
    margin-bottom: 10px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .form button {
    padding: 5px 25px;
    font-size: 20px;
    background: #000000;
    color: rgb(255, 255, 255);
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .posts {
    list-style: none;
    padding: 0;
  }
  
  .posts li {
    background: #4b4949;
    padding: 1rem;
    margin-bottom: 1rem;
    border-radius: 5px;
    box-shadow: 0 0 5px rgba(0,0,0,0.1);
  }
  
  .posts h3 {
    margin-top: 0;
    color: white;
  }
  
  .posts p {
    margin: 0.5rem 0;
    color: white;
  }
  
  .posts button {
    background: #000000;
    color: #ffffff;
    border: none;
    padding: 0.4rem 0.8rem;
    border-radius: 4px;
    cursor: pointer;
    margin-right: 0.5rem;
  }

  .creat-post{
    padding-bottom: 10px;
  }

  .posts-title{
    font-size: 25px;
    color: #000000;
  }

  